.display-engine {
    margin-bottom: 1vh;
    background-color: #00000010;
    border-radius: 10px;
    width: 30vw;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.display-engine:hover {
    background-color: #151E2D;
    transition: 0.2s;
}

.engine-logo {
    margin-right: 1vw;
    border-radius: 50%;
    background-color: #151E2D;
    width: 50px;
    height: 50px;
    min-width: 50px;
    min-height: 50px;
    margin-left: 1vw;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.engine-logo img {
    object-fit: cover;
    width: 80%;
    height: 50%;
}

.display-engine:hover .engine-logo {
    background-color: #FFFFFF;
    transition: 0.2s;
}

.engine-title {
    transition: 0.2s;
    font-size: 1.5rem;
    /* color: #151E2D; */
